import React, { Component } from 'react'
import { Menu } from 'semantic-ui-react'
import { Link } from 'gatsby'

const LinkedItem = ({ children, ...props }) => (
  <Menu.Item as={Link} activeClassName='active' {...props}>{children}</Menu.Item>
)

export default class vertical_menu extends Component {
  state = {}

  render( ) {
    const menuName = this.props.menuName;
    const menuItems = this.props.menuItems;

    return (
      <Menu vertical>
        <Menu.Item>
          <h3>{menuName}</h3>
        </Menu.Item>

        {menuItems.map((menuItem)=>{
            return (
              <LinkedItem key={menuItem.id} to={menuItem.path}
                state={{active_menu :menuItem.name, page_name : menuName}}
              >{menuItem.name}</LinkedItem> //removed exact
            )
        })}
      </Menu>
    )
  }
}